import common from '../../../common/common.js'

export default {
	data() {
		return {
			key:'',
			shopInfo:[],		//用户信息
			catList:[],
		}
	},
	onLoad() {
		var that = this
		var shopInfo = uni.getStorageSync('shopInfo');
		var utype =  uni.getStorageSync('utype');			//用户类型
		
		that.getShopCatList();

	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		console.log('下拉刷新中...');
		
		that.getShopCatList();
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	methods: {
		//删除商品
		delCat(e){
			var that = this
			var catid = e.currentTarget.dataset.catid
			var index = e.currentTarget.dataset.index
			uni.showModal({
				title: '提示',
				content: '确定要删除吗？',
				success: res => {
					if(res.confirm){
						uni.request({
							url: common.sjApiUrl + 'merchant/delCat',
							method: 'GET',
							data: {
								catid:catid
							},
							success: res => {
								var data = res.data
								if(data.code == 0){
									uni.showToast({
										title: '操作成功',
										mask: false,
										duration: 1500
									});
									that.getShopCatList();
								}
							},
							fail: () => {},
							complete: () => {}
						});
					}
				},
			});
		},
		
		//类别列表
		getShopCatList(){
			var that = this
			var shopInfo = uni.getStorageSync('shopInfo');
			//console.log(userInfo);
			uni.request({
				url: common.sjApiUrl + 'merchant/getShopCatList',
				method: 'GET',
				data: {
					shop_id:shopInfo.shop_id
				},
				success: res => {						
					that.catList = res.data.cat_list						
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//
		jumpUrl(e){
			var that = this
			var url = e.currentTarget.dataset.url
			uni.navigateTo({
				url: url
			});
		},
		
	}
}
